		<div class="situation-box">
			<LayoutBox>
				<div class="situation-content" slot="default">
					<BoxItem title="各区年度监管情况">
						<template slot="default">
							<div class="situation-chart" v-loading="mainLoading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0)">
								<div class="charts-content l-charts-content" ref="chartsLeft"></div>
								<ul class="menus-axis" v-show="!mainLoading">
									<li class="menu-name" v-for="(item, index) in areaData" :key="index">
										<span>{{ item.areaName }}</span>
									</li>
								</ul>
								<div class="charts-content r-charts-content" ref="chartsRight"></div>
								<!-- <div class="no-data" v-show="!mainLoading && menusAxis.length === 0"><span>暂无数据</span></div> -->
							</div>
						</template>
					</BoxItem>
				</div>
			</LayoutBox>
		</div>
