</template>

<script>
import CircleLine from '@/components/common/circle-line.vue';
import LayoutBox from '@/components/common/LayoutBox.vue';
import BoxItem from '@/components/common/BoxItem.vue';
import CircleProgress from '@/components/common/circle-progress.vue';

import {deepMerge, deepCopy, maxDecimalPlaces, areaLevelCorAreaId, _initBScroll} from '@/common/fn';

const barOption = {
	title: {
		// text: '地块数（个）:20',
		top: '0',
		left: '10',
		textStyle: {
			color: '#6DC1CB',
			fontSize: 12
		}
	},
	grid: {
		show: false,
		right: '0',
		top: 'center',
		height: '80%',
		width: '75%'
	},
	tooltip: {
		show: true,
		trigger: 'item',
		formatter(a) {
			return `${a.value[0]}：${Math.abs(a.value[1])}万亩`;
		},
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		}
	},
	xAxis: {
		show: false,
		axisLabel: {
			interval: 0,
			textStyle: {
				color: '#6EC2CB',
				fontSize: 12,
				lineHeight: 14
			},
			align: 'center'
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: false
		}
	},
	yAxis: {
		show: false,
		type: 'category',
		name: '单位：',
		nameTextStyle: {
			fontSize: 14,
			color: '#6DC1CB',
			padding: [0, 0, 10, 0]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	dataset: {
		source: [
			// ['landType', 'landCount']
		]
	},
	series: [
		{
			type: 'bar',
			barWidth: '40%',
			barMinHeight: 5,
			label: {
				show: true,
				position: 'left',
				color: '#00F5FF',
				fontSize: 12,
				formatter(a) {
					// console.log(a);
					return Math.abs(a.value[1]);
				},
				offset: [-2, 0]
			},
			z: 10,
			itemStyle: {
				color: {
					type: 'linear',
					x: 1,
					y: 0,
					x2: 0,
					y2: 0,
					colorStops: [
						{
							offset: 0,
							color: '#01C1FF' // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#00F6FF' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			},
			encode: {
				x: 'yearPatrolCount',
				y: 'areaName'
			}
		}
	]
};

var pieOption = {
	color: ['#27ED4E', '#25ECDA', '#E5DA4B', '#E86467'],
	series: [
		{
			type: 'pie',
			center: ['50%', '50%'],
			radius: ['85%', '65%'],
			hoverAnimation: false,
			label: {
				show: false
			},
			labelLine: {
				show: false
			},
			data: []
		}
	]
};

export default {
	data() {
		return {
			menusAxis: [],
			mainLoading: true,
			lawListLoading: false,
			chartsInstance: null,
			chartsCircleInstance: null,
			safeData: {
				yearPatrolCount: 1, // 年度已监管次数
				yearPatrolQualifiedCount: 0,
				yearPatrolTotalCount: 1,
				yearPatrolTotalQualifiedCount: 0, // 年度已监管总合格次数
				yearCheckExcellentCount: 0, // 优次数
				yearCheckGoodCount: 0, // 良次数
				yearCheckQualifiedCount: 0, // 合格次数
				yearCheckUnQualifiedCount: 0 // 不合格次数
			},
			circleChartData: [],
			lawSuperviseList: [],
			checkNameMap: [
				{
					index: 100,
					text: '全部执法结果'
				},
				{
					index: 1,
					text: '优'
				},
				{
					index: 2,
					text: '良'
				},
				{
					index: 3,
					text: '合格'
				},
				{
					index: 4,
					text: '不合格'
				},
				{
					index: 0,
					text: '未知'
				}
			]
		};
	},
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		},
		groupFlag: {
			type: Boolean,
			default: false
		},
		areaData: {
			type: Array,
			default: () => {
				return [];
			}
		}
	},
	computed: {
		chartsLeftRef() {
			return this.$refs.chartsLeft;
		},
		chartsRightRef() {
			return this.$refs.chartsRight;
		},
		EContainer() {
			return this.$refs.EContainer;
		}
	},
	created() {
		this.getStatisticsOnYearByArea();
		this.getLawSuperviseList();
	},
	mounted() {
		this.$nextTick(() => {
			this.initMoudle();
		});
		window.addEventListener('resize', this.adaptationChart);
		this.$once('hook:beforeDestroy', function() {
			window.removeEventListener('resize', this.adaptationChart);
		});
	},
	methods: {
		initMoudle() {
			this.refreshCircleChart(this._initEcharts(this.EContainer, pieOption));
			this.refreshChart([
				this._initEcharts(this.chartsLeftRef, barOption),
				this._initEcharts(
					this.chartsRightRef,
					deepMerge(deepCopy(barOption), {
						title: {
							// text: '面积（万亩）:247.34',
							right: '10',
							left: 'auto'
						},
						grid: {
							left: '0',
							right: 'auto'
						},
						dataset: {
							source: [['areaName', 'yearPatrolObjectCount']]
						},
						series: [
							{
								label: {
									color: '#E5E467',
									position: 'right'
								},
								itemStyle: {
									color: {
										colorStops: [
											{
												offset: 0,
												color: '#E6E768' // 0% 处的颜色
											},
											{
												offset: 1,
												color: '#D29C50' // 100% 处的颜色
											}
										]
									}
								},
								encode: {
									x: 'yearPatrolObjectCount',
									y: 'areaName'
								}
							}
						]
					})
				)
			]);
		},
		refreshRender() {
			if (this.chartsInstance) {
				this.chartsInstance.forEach(chart => {
					chart.resize();
				});
			}
			if (this.chartsCircleInstance) {
				this.chartsCircleInstance.resize();
			}
		},
		handSubTitle() {
			this.$router.push('/lawSuperviseList');
		},
		click(item) {
			this.$emit('click', item);
		},
		// 各区年度监管情况
		getStatisticsOnYearByAreaId(ids) {
			return this.$ajax
				.post(this.$api.safeSupervise.statisticsOnYearByAreaId, {
					farmAreaId: ids,
					farmAreaType: 3
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						return data.obj;
					}
				})
				.catch(e => {
					console.log(e);
				});
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		async refreshChart(chartsInstance) {
			let ids = [];
			this.areaData.forEach(ele => {
				ids.push(ele.id);
			});

			const res = await this.getStatisticsOnYearByAreaId(ids);
			// res.list.reverse();
			let tiledArr = [];
			this.areaData.forEach(ele => {
				let filterTemp = res.filter(i => String(i.areaId) === ele.id);
				if (filterTemp.length) {
					tiledArr.push({
						areaName: ele.areaName,
						...filterTemp[0]
					});
				}
			});

			let numData = tiledArr.map(info => [info.areaName, -info.yearPatrolCount]);
			let objData = tiledArr.map(info => [info.areaName, info.yearPatrolObjectCount]);

			numData = numData.reverse();
			objData = objData.reverse();

			numData.unshift(['areaName', 'yearPatrolCount']);
			objData.unshift(['areaName', 'yearPatrolObjectCount']);

			this.chartsInstance = chartsInstance;
			chartsInstance[0].setOption({
				title: {
					// show: this.menusAxis.length > 0,
					text: `已监管次数（次）`
				},

				dataset: {
					source: numData
				},
				tooltip: {
					formatter(a) {
						return `${a.value[0]}：${Math.abs(a.value[1])}次`;
					}
				}
			});
			chartsInstance[1].setOption({
				title: {
					// show: this.menusAxis.length > 0,
					text: `已监管对象（家）`
				},
				dataset: {
					source: objData
				},
				tooltip: {
					formatter(a) {
						return `${a.value[0]}：${Math.abs(a.value[1])}家`;
					}
				}
			});
			this.adaptationChart();
			this.mainLoading = false;
		},
		async refreshCircleChart(chartsInstance) {
			const res = await this.getStatisticsOnYearByArea();

			let chartData = [];
			chartData[0] = {
				name: '优',
				value: res.yearPatrolTotalCount ? parseFloat(((res.yearCheckExcellentCount / res.yearPatrolTotalCount) * 100).toFixed(2)) : 0
			};
			chartData[1] = {
				name: '良',
				value: res.yearPatrolTotalCount ? parseFloat(((res.yearCheckGoodCount / res.yearPatrolTotalCount) * 100).toFixed(2)) : 0
			};
			chartData[2] = {
				name: '合格',
				value: res.yearPatrolTotalCount ? parseFloat(((res.yearCheckQualifiedCount / res.yearPatrolTotalCount) * 100).toFixed(2)) : 0
			};
			chartData[3] = {
				name: '不合格',
				value: res.yearPatrolTotalCount ? parseFloat(((res.yearCheckUnQualifiedCount / res.yearPatrolTotalCount) * 100).toFixed(2)) : 0
			};

			this.chartsCircleInstance = chartsInstance;
			chartsInstance.setOption({
				series: [
					{
						data: chartData
					}
				]
			});
		},
		getStatisticsOnYearByArea() {
			return this.$ajax
				.post(this.$api.safeSupervise.statisticsOnYearByArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						this.safeData = data.obj;
						return data.obj;
					}
				})
				.catch(e => {
					console.log(e);
				});
		},
		async getLawSuperviseList() {
			this.lawSuperviseList = [];
			this.lawListLoading = true;

			const options = {
				farmAreaId: [this.farmAreaInfo.farmAreaId],
				farmAreaType: this.farmAreaInfo.farmAreaType,
				endTimeStr: '',
				startTimeStr: '',
				keyword: null,
				pageNo: 1,
				pageSize: 100,
				patrolResult: null,
				sortDirection: 1,
				sortField: 0
			};

			const res = await this.$ajax.post(this.$api.govPatrol.lawSuperviseList, options);

			if (res.data.respCode === 0) {
				this.lawListLoading = false;
				this.lawSuperviseList = res.data.obj.list;

				this.$nextTick(async () => {
					this.farmRecordScroll = _initBScroll(this.$refs.lawInspectScroll, {
						scrollX: false,
						freeScroll: false,
						click: true,
						scrollbar: {
							fade: true
						}
					});
				});
			} else {
				this.lawListLoading = false;
			}
		},
		refresh() {
			return this.farmRecordScroll && this.farmRecordScroll.refresh();
		},
		numTofixed(num1, num2) {
			return num1 ? maxDecimalPlaces({value: (num2 / num1) * 100}) : 0;
		},
		formatDate(time, character) {
			const date = new Date(time);

			const YY = date.getFullYear() + character;
			const MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + character;
			const DD = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();

			return YY + MM + DD;
		},
		getColor(patrolResult) {
			switch (patrolResult) {
				case 0:
					return '#ABC0D2';
					break;
				case 1:
					return '#3CCA42';
					break;
				case 2:
					return '#25ECDA';
					break;
				case 3:
					return '#E5DA4B';
					break;
				case 4:
					return '#FF5252';
					break;
				default:
					break;
			}
		},
		adaptationChart() {
			const htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			const opts = {
				series: [
					{
						label: {
							fontSize: 12
						}
					}
				]
			};
			if (htmlWidth > 3000) {
				opts.series[0].label.fontSize = 17;
				if (this.chartsInstance) {
					this.chartsInstance[0].setOption(opts);
					this.chartsInstance[1].setOption(opts);
				}
			} else {
				opts.series[0].label.fontSize = 12;
				if (this.chartsInstance) {
					this.chartsInstance[0].setOption(opts);
					this.chartsInstance[1].setOption(opts);
				}
			}
		}
	},
	components: {
		CircleLine,
		LayoutBox,
		BoxItem,
		CircleProgress
	}
};
</script>

