					display: flex;
					flex-direction: column;
					align-items: center;
					// justify-content: center;
					padding-right: 3%;
					border-right: 2px solid #0f465d;

					span {
						margin-bottom: 12px;
						font-size: 14px;
						color: #71ccde;
					}

					span:nth-child(2) {
						color: #feeb48;
						font-size: 24px;
						font-weight: bold;

						i {
							font-size: 14px;
						}
					}

					span:nth-child(3) {
						i {
							color: #46f262;
						}
					}
				}

				.safe-section-right {
					flex: 1;
					display: flex;
					align-items: center;
					padding-left: 2%;
					box-sizing: border-box;
					width: 100%;

					.echart-container-box {
						position: relative;
						width: 80px;
						height: 80px;

						.echart-container-text {
							position: absolute;
							top: 0;
							left: 0;
							bottom: 0;
							right: 0;
							display: flex;
							align-items: center;
							justify-content: center;

							span {
								color: #9dd1d7;
								font-size: 12px;
								display: inline-block;
								width: 24px;
							}
						}
					}

					.echart-container {
						width: 100%;
						height: 100%;
					}

					.legend {
						flex: 1;
						display: flex;
						flex-direction: column;
						align-content: space-between;
						padding-left: 2%;

						.legend-item {
							display: flex;
							align-items: center;
							margin-bottom: 8px;

							.icon {
								display: flex;
								width: 6px;
								height: 6px;
								border-radius: 50%;
								margin-right: 5px;
							}

							span {
								font-size: 12px;
								color: #71ccde;
							}

							&:nth-child(1) {
								.icon {
									background-color: #27ed4e;
								}
								.num {
									color: #27ed4e;
								}
							}
							&:nth-child(2) {
								.icon {
									background-color: #25ecda;
								}
								.num {
									color: #25ecda;
								}
							}
							&:nth-child(3) {
								.icon {
									background-color: #e5da4b;
								}
								.num {
									color: #e5da4b;
								}
							}
							&:nth-child(4) {
								margin-bottom: 0;

								.icon {
									background-color: #e86467;
								}
								.num {
									color: #e86467;
								}
							}
						}
					}
				}
			}

			.safe-inspect-title {
				width: 100%;
				height: 35px;
				margin-top: 15px;
				background-color: #042940;
				font-size: 16px;
				color: #67bbcd;
				text-align: center;
				font-weight: bold;
				line-height: 35px;
			}

			.law-inspect {
				flex: 1;
				display: flex;
				flex-direction: column;
				height: 100%;
				overflow: hidden;

				.law-inspect-head {
					// border-bottom: 1 solid #32CEBB;
					.text-bold {
						font-size: 14px;
						font-family: Microsoft YaHei;
						font-weight: bold;
						color: #00f6ff;
					}
				}
				.law-inspect-body {
					position: relative;
					overflow: hidden;
					flex: 1;
					height: 100%;

					.text {
						font-size: 14px;
						color: #6dc1cb;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}

					.company-name {
						display: inline-block;
						box-sizing: border-box;
						padding-bottom: 1px;
						margin-right: 10px;
						// text-decoration: underline;
						cursor: pointer;
						border-bottom: 1px solid #6dc1cb;
					}

					.company-name:hover {
						color: #00f6ff;
						font-weight: bold;
						border-bottom: 1px solid #00f6ff;
					}
				}
				.law-inspect-row {
					display: flex;
					border-bottom: 1px solid rgba(50, 206, 187, 0.15);

					.law-inspect-col {
						overflow: hidden;
						display: flex;
						align-items: center;
						height: 46px;
						color: #6dc1cb;
					}
					.law-inspect-col:nth-child(1) {
						flex: 2;
					}
					.law-inspect-col:nth-child(2) {
						flex: 3;
						box-sizing: border-box;
					}
					.law-inspect-col:nth-child(3) {
						flex: 1;
					}
				}
			}
		}
	}

	.situation-box {
		// flex: 3;
		height: 38%;
		margin-top: 15px;

		.situation-content {
			display: flex;
			flex-direction: column;
			height: 100%;
			box-sizing: border-box;

			.situation-chart {
				flex: 1;
				width: 100%;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				@xAxisWidth: 50px;
				position: relative;

				.charts-content {
					width: calc((100% - (@xAxisWidth + 4px)) / 2);
					height: 100%;
					&.l-charts-content {
						/*transform: rotate(180deg);*/
					}
				}
				.menus-axis {
					width: @xAxisWidth;
					background-color: #032337;
					box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.41);
					border-radius: 4px;
					padding: 2px 0;
					height: 80%;
					box-sizing: border-box;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-content: center;
					.menu-name {
						flex: 1;
						text-align: center;
						padding: 0 2px;
						font-size: 12px;
						/*line-height: 22px;*/
						color: #6dc1cb;
						display: flex;
						align-items: center;
						span {
							width: @xAxisWidth;
							box-sizing: border-box;
							overflow: hidden;
							white-space: nowrap;
							text-overflow: ellipsis;
							line-height: 18px;
						}
					}
				}
			}
		}

		/deep/ .el-loading-spinner {
			overflow: hidden;
			margin-top: -45px;
			.iconfont {
				display: block;
				font-size: 40px;
				color: #00f6ff;
				animation: loading 1.25s linear infinite;
				margin-top: 24px;
				margin-left: 8px;
			}
		}
	}

	.no-data {
		// position: absolute;
		// top: 0;
		// left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
		color: #00f6ff;
		font-weight: bold;
	}
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-item span {
		font-size: 20px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-item span:nth-child(2) {
		font-size: 30px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-item span {
		font-size: 20px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-item span:nth-child(3) i {
		font-size: 28px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-right .echart-container-box {
		position: relative;
		width: 140px;
		height: 140px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-right .echart-container-box .echart-container-text span {
		font-size: 20px;
		width: 42px;
		margin-top: 2px;
		line-height: 26px;
	}
	.safeSupervise-right .safe-box .safe-content .safe-section .safe-section-right .legend .legend-item span {
		font-size: 22px;
		line-height: 26px;
	}
	.safeSupervise-right .safe-box .safe-content .law-inspect .law-inspect-body .text {
		font-size: 20px;
	}
	.safeSupervise-right .safe-box .safe-content .law-inspect .law-inspect-head .text-bold {
		font-size: 22px;
	}
}
</style>



